<template>
	<p class="mb-0 mt-4">
		Get_form component
		{{ action.functionParams }} {{ action.state }}
	</p>

	<div class="row form-pagination">
		<div class="col-12 col-sm-3 text-center text-sm-left">
			<button class="btn btn-white btn-lg" @click="prev()">
				Previous
			</button>
		</div>
		<div class="col-12 col-sm-3 text-center">
			<h4 class="mt-2">
				{{ tab_current }} / {{ tab_last }}
			</h4>
		</div>
		<div class="col-12 col-sm-3 text-center">
			<button class="btn btn-white btn-lg" @click="next()">
				Next
			</button>
		</div>
		<div class="col-12 col-sm-3 text-center text-sm-right">
			<button class="btn btn-primary btn-lg" @click="validate()">
				Validate
			</button>
		</div>
	</div>

	<h3 v-if="valid" class="text-center">Form is VALID</h3>
	<Tabs :active_tab="active_tab" :tab_current="tab_current" :errors="errors" />

</template>

<script>
	import Get_form from '@/logic/requests/Get_form.js';
	import Tabs from '@/components/forms/Tabs.vue';

	export default {
		props: {
			action: Object,
		},
		components: {
			Tabs,
		},
		data() {
			return {
				group_form: {},
				errors: {},
				tab_current: 1,
				tab_last: 0,
				valid: false,
			}
		},
		mounted() {
			Get_form.osmappi_get_form(this.action).then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					this.group_form = response.result.data.resultData.form;
					this.tab_last = Object.keys(this.group_form).length;
				}
			);
		},
		methods: {
			prev() {
				if (this.tab_current <= 1) {
					return;
				}
				this.tab_current--;
			},
			next() {
				if (this.tab_current >= this.tab_last) {
					return;
				}
				this.tab_current++;
			},
			validate() {
				Get_form.osmappi_validate_form(this.action).then(
					(response) => {
						this.errors = response.result.data.resultData.form_validation_error_elements;
						if (response.result.data.resultData.validform === false) {
							this.show_first_tab_with_error();
						}
						else if (response.result.data.resultData.validform === true) {
							this.valid = true;
						}
					}
				);
			},
			show_first_tab_with_error() {
				for (var tab_number in this.group_form) {
					if ( ! this.group_form.hasOwnProperty( tab_number ) ) { continue; }
					for (var input_order in this.group_form[tab_number]) {
						if ( ! this.group_form[tab_number].hasOwnProperty( input_order ) ) { continue; }
						for (var input_id in this.group_form[tab_number][input_order]) {
							if ( ! this.group_form[tab_number][input_order].hasOwnProperty( input_id ) ) { continue; }
							if (this.errors.hasOwnProperty(input_id)) {
								this.tab_current = parseInt(tab_number, 10);
								return;
							}
						}
					}
				}
			},
		},
		computed: {
			active_tab() {
				var tab = this.group_form[this.tab_current];
				return tab;
			},
		},
	}
</script>
